@import "./baseline.scss";

#page-header {
  background-color: rgba(128, 0, 128, 0.7);
  color: #fefefe;
  font-size: 2rem;
  padding: 0.25rem 0.5rem;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding-left: 4.75vw;
}

#page-content {
  background-color: #333;
}

#actual-content {
  background-color: #222;
  min-height: 100vh;
  max-width: 90vw;
  margin: 0 auto;
  padding-top: 1rem;
  padding-bottom: 2rem;
}

#terminal-container {
  height: 200px;
  max-width: 600px;
  margin: 3rem auto 200px;
}

#terminal {
  max-height: 200px;
  max-width: 600px;
  border: 1px solid pink;
  resize: none;
  background-color: rgba(52, 52, 52, 1);
  display: block;
  padding: 0.5rem;
  color: #efefef;
  font-size: 1.5rem;
  overflow-y: scroll;
  overflow-x: hidden;
  scrollbar-color: silver black;
  scrollbar-width: thin;
  &::-webkit-scrollbar {
    width: 12px;
  }
  &::-webkit-scrollbar-track {
    background: black;
  }
  &::-webkit-scrollbar-thumb {
    background-color: silver;
    border-radius: 20px;
    border: 3px solid black;
  }
  &:after {
    content: '|';
    color: lightpink;
    font-size: 2rem;
    opacity: 1;
    animation: blinking 0.75s infinite;
  }
}

#breaking-the-habit-container {
  margin: 1rem auto 0;
  max-width: 560px;
  overflow: hidden;
}

#breaking-the-habit {
  margin: 0 auto 0;
  display: block;
  overflow: hidden;
}

.pixelate {
  font-family: 'VT323', monospace;
}

header.pixelate {
  background-color: #000;
  color: #efefef;
  font-size: 2rem;
  padding: 0.2rem 0.25rem;
  line-height: 1.5;
  color: purple;
  border-radius: 0.25rem 0.25rem 0rem 0rem;
}

.content.pixelate {
  background-color: #dedede;
  padding: 0.25rem 0.5rem;
  margin-top: 0.25rem;

  h3 {
    margin-bottom: 0.2rem;
  }
}

.embed-container {
  position: relative; padding-bottom: 56.25%; height: 0; overflow: hidden; max-width: 100%;
}
.embed-container iframe, .embed-container object, .embed-container embed {
  position: absolute; top: 0; left: 0; width: 100%; height: 100%;
}

.violet-link {
  text-decoration: none;
  color: darken($color: violet, $amount: 20);
  font-size: 1.23rem;
}

@keyframes blinking {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}